.sidebar {
  background-color: #141414; /* Set your preferred background color #1c4e80*/
  color: #ffffff; /* Set your preferred text color */
  width: 250px; /* Set the width of the sidebar */
  height: 100vh; /* Make the sidebar take the full height */
  overflow-y: auto; /* Add scroll if content exceeds height */
  border-right: 1px solid #000000; /* Add a vertical line on the right side */
}

.sidebar-item {
  align-items: center; /* Center items vertically */
  padding: 15px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  text-align: left; /* Align text to the left */
  height: 70px; /* Set the height of the sidebar item */
  border-bottom: 1px solid #000000; /* Add a border between items */
}

.sidebar-item:hover {
  background-color: #1717186f; /* Set the background color on hover */
}

.search-bar-container {
  display: flex;
  justify-content: space-between;
  padding: 0px; /* Adjust the padding as needed */
  border-right: 1px solid #000000; /* Add a vertical line on the right side */
}

.search-bar {
  flex: 1;
  padding: 15px; /* Adjust the padding as needed */
}

.add-password-button img {
  width: 35px; /* Adjust the width as needed */
  height: 35px; 
}

.sidebar-item {
  cursor: pointer;
}

.site-name,
.email {
  display: block; /* Ensure that both siteName and email are displayed as block-level elements */
}

.site-name {
  font-weight: bold; /* Optional: Make the site name bold or customize as needed */
}

.email {
  color: #cecece; /* Optional: Customize the color of the email for better differentiation */
}

.add-password-button svg {
  width: 50px; /* Adjust the width as needed */
  height: 50px; /* Adjust the height as needed */
  fill: #2176ff; /* Adjust the fill color as needed */
  padding: 10px;
  transition: fill 0.3s ease; /* Add a transition effect */
}

.add-password-button:hover svg {
  fill: #0051ff; /* Set the color for the hover state */
}

button.add-password-button {
  background-color: transparent !important;
  border: none !important;
  cursor: pointer;
  padding: 0 !important;
}

button.add-password-button {
  fill: none !important;
  stroke: #00000000 !important; /* Adjust the stroke color as needed */
}

